<template>
	 <div> 
    	<layer-comp></layer-comp>
	    <transition @before-leave="beforeLeave" @after-enter="afterEnter"> 

			<div id="sw-wrapper" class="sw-wrapper" v-show="spinWheelFlag">
			 	<div class="sw-header">
			 		<span class="sw-item" @click="handleChangeState">取消</span>
				 	<span class="sw-item">选择</span>
				 	<span class="sw-item" @click="handleSetResult">确定</span>
				 </div>
			 	<div class="sw-spins-wrapper">
			 		<div class="sw-spins">
				 		<div class="sw-spin" v-for="(spin,spinIndex) in spinDatas" v-bind:key="spinIndex">
				 			<ul> 
				 				<li  v-bind:class="{'selected' : spin.selectedIndex==slotIndex}"  v-for="(slot,slotIndex) in spin.slotDatas" v-bind:key="slotIndex" v-text="slot.swName"></li>
				 			</ul>
				 		</div>
					 	 
					</div>
				</div>

			 	<div class="sw-frame"  @touchstart="handleTouchStart($event)"  @touchmove="handleTouchMove($event)" @touchend="handleTouchEnd($event)"></div>
			 	<div class="selected-area"></div>
			</div>

	    </transition>

	 </div>
</template>

<script>
	import "./spinWheel.scss";
	import layerComp from "@/components/layer/layer.vue";
	import crlUtil from "@/crlUtil.js";
	import {
	    mapState
	} from "vuex";
    export default {
        data() {
            let self = this;
            return {
                spinDatas: [],
            }
        },
        props: {},
        created() {
            // console.log("---------- created-----------");
        },
        mounted() {
            // console.log("---------- mounted-----------");

            let self = this;
            /*******  slot原始值数据  ******/
            self.screenWidth = screen.width; //屏幕宽度
            self.spinColumnNum = -1; // 列数 spinDatas的数量 和 spinDatas.length对应
            self.spinCtros = [];

            self.activeSpin = -1; //当前触发slot
            self.spinMoving = false; //slots中是否有在转动 第一个slot在动 第二个就不要动了 
            self.slotHeight = 40;
            /*******  slot原始值数据  ******/


            self.initSlotsDisplay();

        },

        computed: {
            ...mapState({
                spinWheelFlag: state => state.compCtro.spinWheelFlag,
                spinWheelOptions: state => state.compCtro.spinWheelOptions,
            }),
        },

        components: {
            layerComp,
        },
        watch: {
            spinWheelFlag() { //让蒙版和spinWheel同步
                let self = this;
                self.$store.dispatch('compCtro/changeLayerState');
            }
        },

        methods: {

            handleChangeState() {
                let self = this;
                if (self.spinDatas.length == 0) {
                    return;
                }
                self.$store.dispatch('compCtro/changeSpinWheelState');
            },
            
            handleSetResult() {
                let self = this;
                let setSpinWheelResult = [];
                for(let spin of self.spinDatas){
                    setSpinWheelResult.push(spin.selectedIndex);
                }
                console.log(setSpinWheelResult);
                self.$store.dispatch('compCtro/setSpinWheelResult',setSpinWheelResult);
                self.$store.dispatch('compCtro/changeSpinWheelState');
            },

            beforeLeave(el) {
                el.style.webkitTransform = `translate3d(0,0,0)`;
                el.style.transform = `translate3d(0,0,0)`;
            },
            afterEnter(el) {
                el.style.webkitTransform = `translate3d(0,-100%,0)`;
                el.style.transform = `translate3d(0,-100%,0)`;
            },


            initSlotsDisplay() {
                let self = this;

                //slotCtrl 初始化
                self.$nextTick(function() {

                    let data = [];
                    let data1 = {};
                    let data2 = {};
                    let data3 = {};


                    let spinDatas = self.spinWheelOptions.spinDatas;
                    let spinID = self.spinWheelOptions.spinID;

                    self.$store.dispatch('compCtro/setSpinWheelResult', self.spinWheelOptions.spinID);

                    self.spinColumnNum = self.spinWheelOptions.spinColumnNum;
                    self.slotWidth = self.screenWidth / self.spinColumnNum;

                    data1 = self.getSingleSlotData(spinDatas, spinID[0]);
                    data.push(data1);

                    let linkDatas = spinDatas[data1.selectedIndex].linkDatas;
                    if (crlUtil.isNotEmpty(linkDatas)) {

                        data2 = self.getSingleSlotData(linkDatas, spinID[1]);
                        data.push(data2);

                        let linklinkDatas = linkDatas[data2.selectedIndex].linkDatas;
                        if (crlUtil.isNotEmpty(linklinkDatas)) {
                            data3 = self.getSingleSlotData(linklinkDatas, spinID[2]);
                            data.push(data3);
                        }

                    }

                    //展示数据
                    self.spinDatas = data;


                    self.$nextTick(function() {
                        self.refreshSpinCtros();
                    });

                });
            },

            getSingleSlotData(slotDatas, slotDataID) {
                let reCombine = {};
                //对传进来的数据进行初始化
                reCombine.selectedIndex = 0;
                reCombine.slotDatas = [];
                slotDatas.forEach((slotData, slotIndex, a) => {

                    let slot = {};

                    // TODO swName作为标识不是很好 最好找一个swCode
                    slot.swName = slotData.swName;
                    // 。。。 其他属性 不包含linkData的数据

                    if (slotData.swName == slotDataID) {
                        reCombine.selectedIndex = slotIndex;
                    }
                    reCombine.slotDatas.push(slot);

                });

                return reCombine;
            },

            refreshSpinCtros() {
                let self = this;
                //关联dom与数据到ctro中
                self.spinCtros = [];
                self.spinDatas.map((spin, spinIndex) => {
                    let spinCtro = {};
                    spinCtro.index = spinIndex;
                    spinCtro.el = document.getElementsByClassName("sw-spin")[spinIndex];
                    spinCtro.slotYPosition = 0; //只有slotYPosition是动态值 其他都是确认的
                    spinCtro.slotMaxLength = spin.slotDatas.length * self.slotHeight;
                    self.spinCtros.push(spinCtro);

                    //滚动界面到spinID指定的选项
                    self.setWheelStatic(spinCtro, -1, spin.selectedIndex);
                });
            },

            handleTouchStart(e) {
                let self = this;

                self.lockScreen(e);

                let xPos = e.targetTouches[0].clientX;
                self.startY = e.targetTouches[0].clientY;

                self.activeSpin = Math.floor(xPos / self.slotWidth);

            },

            handleTouchMove(e) {
                let self = this;
                self.lockScreen(e);
                let eachMoveY = e.targetTouches[0].clientY - self.startY; //每次前进的多少 速度

                self.setWheelMove(eachMoveY);

                self.startY = e.targetTouches[0].clientY;

            },

            handleTouchEnd(e) {
                let self = this;
                self.lockScreen(e);

                let spinCtro = self.spinCtros[self.activeSpin];
                let slotYPosition = spinCtro.slotYPosition;

                let flag = slotYPosition != 0 ? (slotYPosition / Math.abs(slotYPosition)) : 0;
                let numb = Math.floor((Math.abs(slotYPosition)) / self.slotHeight); //-5的目的是 当移动过少 还是按前一个算
                // console.log(slotYPosition+"-----"+numb);
                // TODO 间距滑动还有待处理 

                self.setWheelStatic(spinCtro, flag, numb);
            },

            setWheelMove(eachMoveY) { // wheel随着手指滑动而移动
                let self = this;
                let spinCtro = self.spinCtros[self.activeSpin];
                spinCtro.slotYPosition = spinCtro.slotYPosition + eachMoveY;
                let pos = Math.min((self.slotHeight - 5), spinCtro.slotYPosition); // 防止移动过多 最多向下移动一个距离 -5的目的是为了防止整出的情况出现
                pos = Math.max(-(spinCtro.slotMaxLength - 5), pos); // 防止移动过多 最多向上移动整个长度 -5的目的是为了防止整出的情况出现

                spinCtro.el.style["webkitTransform"] = 'translate3d(0, ' + pos + 'px, 0)';
                spinCtro.el.style["transform"] = 'translate3d(0, ' + pos + 'px, 0)';

                spinCtro.slotYPosition = pos; //从slot来 回slot去

            },

            setWheelStatic(spinCtro, flag, numb) { // 当手离开Wheel的时候 指定 (slot wheel) 进行 固定位置 
                let self = this;

                let pos = flag * numb * self.slotHeight;

                spinCtro.el.style["webkitTransform"] = 'translate3d(0, ' + pos + 'px, 0)';
                spinCtro.el.style["transform"] = 'translate3d(0, ' + pos + 'px, 0)';

                spinCtro.slotYPosition = pos; //从slot来 回slot去

                // 不相等 刷新spin的slot
                if (self.spinDatas[spinCtro.index].selectedIndex != numb) {

                    self.spinDatas[spinCtro.index].selectedIndex = numb;
                    self.changeLinkDatas(spinCtro.index);
                }
            },

            changeLinkDatas(changeLeve) {

                let self = this;
                let spinDatas = self.spinWheelOptions.spinDatas;

                let data1 = self.spinDatas[0];
                let data2 = {};
                let data3 = {};

                if (changeLeve < 1) { //说明改变的是第一层 下两层都要调整

                    let linkDatas = spinDatas[data1.selectedIndex].linkDatas;

                    if (crlUtil.isNotEmpty(linkDatas)) {

                        data2 = self.getSingleSlotData(linkDatas, "");

                        self.$set(self.spinDatas, 1, data2);

                        let linklinkDatas = linkDatas[data2.selectedIndex].linkDatas;
                        if (crlUtil.isNotEmpty(linklinkDatas)) {
                            data3 = self.getSingleSlotData(linklinkDatas, "");
                            self.$set(self.spinDatas, 2, data3);
                        }

                    }

                } else if (changeLeve < 2) { //说明改变的是第二层 下一层都要调整

                    let linkDatas = spinDatas[data1.selectedIndex].linkDatas;

                    if (crlUtil.isNotEmpty(linkDatas)) {

                        let data2 = self.spinDatas[1];

                        let linklinkDatas = linkDatas[data2.selectedIndex].linkDatas;
                        if (crlUtil.isNotEmpty(linklinkDatas)) {
                            data3 = self.getSingleSlotData(linklinkDatas, "");
                            self.$set(self.spinDatas, 2, data3);
                        }
                    }
                }

                self.refreshSpinCtros();

            },


            lockScreen(e) {
                e.preventDefault();
                e.stopPropagation();
            },
            handleNoop() {
                //noop
            },
        },

    }
</script> 


