<template>
	<div class="crl-layer" v-show="layerFlag"></div>
	 <!-- v-on:click="handleChangeState()" -->
</template>

<script>

	import {mapState} from 'vuex'  

	export default {
		data() {
			var self = this;
			return {
				touchmoveFlag:true,
			}
		},
		props: {
		},
		created() {
			// console.log("---------- created-----------");
		}, 
		mounted() {
			// console.log("---------- mounted-----------"); 
		},

    	computed:{ 
	        ...mapState({
	            layerFlag: state => state.compCtro.layerFlag
	        }), 
	    },

		watch: {
			layerFlag(newValue){
				if(newValue && this.touchmoveFlag){
					this.touchmoveFlag =false;
					document.getElementsByClassName("crl-layer")[0].addEventListener("touchmove", function(e) {
						e.preventDefault();
						return false;
					}, false);
				}
			}
		},

		methods: {
			handleChangeState(){
				this.$store.dispatch('compCtro/changeLayerState');
			},
			handleNoop: function () {
				//noop
			},
		},

	}
</script>


<style>
	.crl-layer{
		position: fixed;
		bottom: 0;
		width: 100%;
		background-color: rgba(0,0,0,.5);
		top: 0;
		z-index: 100;
		-webkit-transition: all ease-in-out 50ms;
		transition: all ease-in-out 50ms;
		-webkit-tap-highlight-color: rgba(0,0,0,0);
		tap-highlight-color: transparent;
	}
</style>



